import { IEditorUi } from "./IEditorUi";

/**
 * 底部栏
 */
export class Footer {
    editorUi: IEditorUi;
    /**
     * 容器
     */
    container: HTMLDivElement;

    constructor(editorUi: IEditorUi, container: HTMLDivElement) {
        this.editorUi = editorUi;
        this.container = container;
    }
}